import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { PermissionViewModel } from "../../Model/ViewModel/PermissionViewModel";

/**
 * Generated class for the PermissionModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-permission-add-edit-modal",
  templateUrl: "permission-add-edit-modal.html"
})
export class PermissionAddEditModalPage {
  item: any;
  mode: string;
  title: string;
  CanEditPermission: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
    this.item = new PermissionViewModel();
    this.item.permission_no = navParams.data.item.permission_no;
    this.item.permission_desc = navParams.data.item.permission_desc;
    this.item.permission_func_desc = navParams.data.item.permission_func_desc;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.CanEditPermission = navParams.data.CanEditPermission;
    this.mode = navParams.data.mode;
    if (this.mode === "PUT") this.title = "修改";
    else this.title = "新增";
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad PermissionModalPage");
  }
}



